/* adapted from https://github.com/geekytime/segoe-symbol-css */
[class^="sgicon-"],
[class*=" sgicon-"] {
  font-family: 'Segoe UI Symbol';
  font-weight: normal;
  font-style: normal;
  text-decoration: inherit;
  -webkit-font-smoothing: antialiased;
  /* sprites.less reset */
  display: inline;
  width: auto;
  height: auto;
  line-height: normal;
  vertical-align: baseline;
  background-image: none;
  background-position: 0% 0%;
  background-repeat: repeat;
  margin-top: 0;
}
/* more sprites.less reset */
.sgicon-white,
.nav-pills > .active > a > [class^="sgicon-"],
.nav-pills > .active > a > [class*=" sgicon-"],
.nav-list > .active > a > [class^="sgicon-"],
.nav-list > .active > a > [class*=" sgicon-"],
.navbar-inverse .nav > .active > a > [class^="sgicon-"],
.navbar-inverse .nav > .active > a > [class*=" sgicon-"],
.dropdown-menu > li > a:hover > [class^="sgicon-"],
.dropdown-menu > li > a:hover > [class*=" sgicon-"],
.dropdown-menu > .active > a > [class^="sgicon-"],
.dropdown-menu > .active > a > [class*=" sgicon-"],
.dropdown-submenu:hover > a > [class^="sgicon-"],
.dropdown-submenu:hover > a > [class*=" sgicon-"] {
  background-image: none;
}
[class^="sgicon-"]:before,
[class*=" sgicon-"]:before {
  text-decoration: inherit;
  display: inline-block;
  speak: none;
}
/* makes sure icons active on rollover in links */
a [class^="sgicon-"],
a [class*=" sgicon-"] {
  display: inline-block;
}
/* makes the font 33% larger relative to the icon container */
